@import url(https://fonts.googleapis.com/css2?family=Allison&family=Kaisei+HarunoUmi:wght@700&family=Lobster&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  background-image: linear-gradient(to top, #ff0844 0%, #ffb199 100%);
}




.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-text{
  width: 50%;
  border: none;
  padding: 10px;
}

.search-button{
  background: lightcoral;
  border: none;
  padding: 10px 20px;
  color: white;
}

.recipes{
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.head{
  color: rgb(255, 252, 251);
  margin-top: 0px;
  padding-top: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Lobster', cursive;
  font-size: 70px;
}

a{
  color: #08ffad;
  padding-left: 15px;
  padding-right: 15px;
  text-decoration: none;
}

a:hover{
  letter-spacing: 5px;
}

.foot{
  display: flex;
  justify-content: center;
  align-items: center;
  color: azure;
}
